// 科目名称
function subjectFilter(subjectName: number) {
    if (subjectName === 0) {
        return '法语';
    } else if (subjectName === 1) {
        return '语文';
    } else if (subjectName === 2) {
        return '数学';
    } else if (subjectName === 3) {
        return '英语';
    }
}
// 年级
function gradeFilter(grade: number) {
    switch (grade) {
        case 1:
            return '一年级';
        case 2:
            return '二年级';
        case 3:
            return '三年级';
        case 4:
            return '四年级';
        case 5:
            return '五年级';
        case 6:
            return '六年级';
    }
}
// 上架草稿过虑器
function stateFilter(subjectStatus: number) {
    if (subjectStatus === 0) {
        return '草稿';
    } else {
        return '上架';
    }
}
// 上下线
function updateFilter(status: number) {
    if (status === 0) {
        return '上架';
    } else if (status === 1) {
        return '下架';
    }
}
// 上下线
function updateFilterr(status: any) {
    if (status === '草稿') {
        return '上架';
    } else if (status === '上架') {
        return '下架';
    }
}
// 难度等级过滤器
function difficultyFilter(difficulty: number) {
    switch (difficulty) {
        case 1:
            return '一级';
        case 2:
            return '二级';
        case 3:
            return '三级';
        case 4:
            return '四级';
        case 5:
            return '五级';
        case 6:
            return '六级';
    }
}
// 检查时间单位，小于十位数的添加0
const checkTime = (i: number | string) => {
    if (i < 10) {
        i = '0' + i;
    }
    return i;
};
// 毫秒数转换日期至此格式年-月-日
function datePipeYml(msec: number) {
    const d = new Date(msec);
    const y = d.getFullYear();
    const m = checkTime(d.getMonth() + 1);
    const l = checkTime(d.getDate());
    return `${y}-${m}-${l}`;
}
// 毫秒数转换日期至此格式小时：分
function datePipeHm(msec: number) {
    const d = new Date(msec);
    const h = checkTime(d.getHours());
    const min = checkTime(d.getMinutes());
    return `${h}:${min}`;
}
// 分类
function classification(params: number) {
    if (params === 0) {
         return 'banner咨询';
    } else if (params === 1) {
        return 'card咨询';
    }
}
export let filters: { [key: string]: (key: number) => string | undefined } = {
    subjectFilter,
    gradeFilter,
    stateFilter,
    difficultyFilter,
    updateFilter,
    updateFilterr,
    datePipeYml,
    datePipeHm,
    classification,
};
