import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css';
import { library } from '@fortawesome/fontawesome-svg-core'; // 图标库
// font-awesome-icon
// question-circle
import {
    faArrowLeft,
    faBookmark,
    faMobileAlt,
    faUnlockAlt,
    faCheck,
    faCircle,
    faShieldAlt,
    faQuestionCircle,
} from '@fortawesome/free-solid-svg-icons'; // 按需导入
import { faWeixin, faWeibo, faQq } from '@fortawesome/free-brands-svg-icons';
import {
    FontAwesomeIcon,
    FontAwesomeLayers,
} from '@fortawesome/vue-fontawesome';
import Header from '@/components/Header.vue';
import './assets/public/reset.scss'; // 样式重置

library.add(
    faArrowLeft,
    faBookmark,
    faMobileAlt,
    faShieldAlt,
    faUnlockAlt,
    faCheck,
    faWeixin,
    faWeibo,
    faQq,
    faCircle,
    faQuestionCircle,
); // 添加图标
Vue.component('font-awesome-icon', FontAwesomeIcon);
Vue.component('font-awesome-layers', FontAwesomeLayers);
Vue.component('Header', Header);
Vue.use(Mint);

Vue.config.productionTip = false;

// 过滤器@q
import { filters } from '@/assets/public/filters.ts';
// 过滤器@q
Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key]);
});

new Vue({
    router,
    store,
    render: (h) => h(App),
}).$mount('#app');
